document.addEventListener('DOMContentLoaded', function() {
    
    // تهيئة السنة الحالية
    document.getElementById('currentYear').textContent = new Date().getFullYear();
    
    // السلايدر
    const slider = {
        slides: document.querySelectorAll('.slide'),
        dots: document.querySelectorAll('.dot'),
        prevBtn: document.querySelector('.slider-prev'),
        nextBtn: document.querySelector('.slider-next'),
        progressBar: document.querySelector('.progress-bar'),
        currentSlide: 0,
        interval: null,
        speed: 5000, // 5 ثواني لكل شريحة
        
        init: function() {
            if (this.slides.length === 0) return;
            
            // بدء التشغيل التلقائي
            this.startAutoSlide();
            
            // أحداث الأزرار
            this.prevBtn.addEventListener('click', () => this.changeSlide(-1));
            this.nextBtn.addEventListener('click', () => this.changeSlide(1));
            
            // أحداث النقاط
            this.dots.forEach((dot, index) => {
                dot.addEventListener('click', () => this.goToSlide(index));
            });
            
            // إيقاف التشغيل التلقائي عند التمرير
            document.querySelector('.hero-slider').addEventListener('mouseenter', () => {
                this.stopAutoSlide();
            });
            
            document.querySelector('.hero-slider').addEventListener('mouseleave', () => {
                this.startAutoSlide();
            });
            
            // تحديث شريط التقدم
            this.updateProgressBar();
        },
        
        changeSlide: function(direction) {
            this.currentSlide += direction;
            
            if (this.currentSlide < 0) {
                this.currentSlide = this.slides.length - 1;
            } else if (this.currentSlide >= this.slides.length) {
                this.currentSlide = 0;
            }
            
            this.updateSlides();
            this.updateDots();
            this.updateProgressBar();
            this.resetAutoSlide();
        },
        
        goToSlide: function(index) {
            this.currentSlide = index;
            this.updateSlides();
            this.updateDots();
            this.updateProgressBar();
            this.resetAutoSlide();
        },
        
        updateSlides: function() {
            this.slides.forEach(slide => {
                slide.classList.remove('active');
            });
            this.slides[this.currentSlide].classList.add('active');
        },
        
        updateDots: function() {
            this.dots.forEach(dot => {
                dot.classList.remove('active');
            });
            this.dots[this.currentSlide].classList.add('active');
        },
        
        updateProgressBar: function() {
            if (this.progressBar) {
                this.progressBar.style.transition = 'none';
                this.progressBar.style.width = '0%';
                
                setTimeout(() => {
                    this.progressBar.style.transition = `width ${this.speed}ms linear`;
                    this.progressBar.style.width = '100%';
                }, 50);
            }
        },
        
        startAutoSlide: function() {
            this.stopAutoSlide();
            this.interval = setInterval(() => this.changeSlide(1), this.speed);
        },
        
        stopAutoSlide: function() {
            if (this.interval) {
                clearInterval(this.interval);
                this.interval = null;
            }
        },
        
        resetAutoSlide: function() {
            this.stopAutoSlide();
            this.startAutoSlide();
        }
    };
    
    // تهيئة السلايدر
    slider.init();
    
    // شريط التنقل
    const header = document.getElementById('mainHeader');
    const burger = document.getElementById('burger');
    const navLinks = document.getElementById('navLinks');
    
    // التمرير
    window.addEventListener('scroll', function() {
        if (window.scrollY > 100) {
            header.classList.add('scrolled');
        } else {
            header.classList.remove('scrolled');
        }
        
        // زر العودة للأعلى
        const scrollToTop = document.getElementById('scrollToTop');
        if (window.scrollY > 500) {
            scrollToTop.classList.add('show');
        } else {
            scrollToTop.classList.remove('show');
        }
    });
    
    // قائمة الهاتف
    burger.addEventListener('click', function() {
        burger.classList.toggle('active');
        navLinks.classList.toggle('active');
        document.body.classList.toggle('menu-open');
    });
    
    // إغلاق القائمة عند النقر على رابط
    document.querySelectorAll('.nav-link').forEach(link => {
        link.addEventListener('click', () => {
            burger.classList.remove('active');
            navLinks.classList.remove('active');
            document.body.classList.remove('menu-open');
        });
    });
    
    // زر العودة للأعلى
    document.getElementById('scrollToTop').addEventListener('click', function() {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    });
    
    // مؤشر التنقل
    const indicator = document.getElementById('indicator');
    const navLinksAll = document.querySelectorAll('.nav-link');
    
    function updateIndicator() {
        const activeLink = document.querySelector('.nav-link.active');
        if (activeLink && indicator) {
            indicator.style.width = activeLink.offsetWidth + 'px';
            indicator.style.left = (activeLink.offsetLeft + activeLink.offsetWidth) + 'px';
            indicator.style.transform = 'translateX(-100%)';
        }
    }
    
    navLinksAll.forEach(link => {
        link.addEventListener('click', function(e) {
            navLinksAll.forEach(l => l.classList.remove('active'));
            this.classList.add('active');
            updateIndicator();
        });
    });
    
    // تحديث المؤشر عند تحميل الصفحة وتغيير الحجم
    window.addEventListener('load', updateIndicator);
    window.addEventListener('resize', updateIndicator);
    
    // نموذج الاتصال
    const contactForm = document.getElementById('messageForm');
    if (contactForm) {
        contactForm.addEventListener('submit', function(e) {
            e.preventDefault();
            
            const formData = new FormData(this);
            const formMessage = document.getElementById('formMessage');
            
            // محاكاة الإرسال
            formMessage.innerHTML = `
                <div style="background: #4CAF50; color: white; padding: 15px; border-radius: 8px; margin: 15px 0;">
                    <i class="fas fa-check-circle"></i> تم إرسال رسالتك بنجاح! سنتواصل معك قريباً.
                </div>
            `;
            
            // إعادة تعيين النموذج
            setTimeout(() => {
                contactForm.reset();
                formMessage.innerHTML = '';
            }, 5000);
        });
    }
    
    // التمرير السلس
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
        anchor.addEventListener('click', function(e) {
            const targetId = this.getAttribute('href');
            if (targetId === '#') return;
            
            const targetElement = document.querySelector(targetId);
            if (targetElement) {
                e.preventDefault();
                window.scrollTo({
                    top: targetElement.offsetTop - 80,
                    behavior: 'smooth'
                });
            }
        });
    });
    
    // تحديث المؤشر الأولي
    setTimeout(updateIndicator, 100);
    
    // إضافة تأثيرات للبطاقات
    const serviceCards = document.querySelectorAll('.service-card');
    serviceCards.forEach(card => {
        card.addEventListener('mouseenter', function() {
            this.style.transform = 'translateY(-10px)';
        });
        
        card.addEventListener('mouseleave', function() {
            this.style.transform = 'translateY(0)';
        });
    });
    
    // تحسين الأداء للصور
    const images = document.querySelectorAll('img');
    images.forEach(img => {
        img.setAttribute('loading', 'lazy');
    });
});